@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap");
:root {
    --page-size: 1;
    --enlarge-size: 1;

    --bg-color: rgb(40, 40, 40);
    --fg-color: blanchedalmond;
}

.theme-black {
    --bg-color: rgb(40, 40, 40);
    --fg-color: blanchedalmond;
}

.theme-light {
    --bg-color: blanchedalmond;
    --fg-color: rgb(40, 40, 40);
}

body {
    font-family: "Source Code Pro", "Noto Sans TC", sans-serif;
    padding: 0;
    margin: 0;
    -webkit-text-size-adjust: 100%;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    background-color: var(--bg-color);
}

.page {
    position: relative;
    padding: 0;
    width: calc(297.5mm);
    height: calc(210mm);
    background-color: var(--bg-color);
    color: var(--fg-color);
    break-inside: avoid;
    margin: 0;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(var(--page-size), var(--page-size));
}

.page-num {
    position: absolute;
    bottom: calc(8mm);
    right: calc(16mm);
}

.text-xs {
    font-size: calc(4.5mm);
}

.text-sm {
    font-size: calc(5.75mm);
}

.text-base {
    font-size: calc(7mm);
}

.text-lg {
    font-size: calc(8.5mm);
}

.text-xl {
    font-size: calc(10mm);
}

.text-2xl {
    font-size: calc(11.5mm);
}

.text-3xl {
    font-size: calc(13mm);
}

.text-4xl {
    font-size: calc(15mm);
}

.text-5xl {
    font-size: calc(17.5mm);
}

.text-6xl {
    font-size: calc(20mm);
}

.none {
    display: none;
}

p,
span,
div {
    padding: 0;
    margin: 0px;
}

a {
    color: var(--fg-color);
}

table {
    border-collapse: collapse;
}

td {
    padding-top: 2mm;
    padding-bottom: 2mm;
    border-bottom: 2px solid;
}

.page-back-button {
    position: absolute;
    right: 12vw;
    bottom: 6vw;
    width: 6vw;
    height: 6vw;
}

.page-forward-button {
    position: absolute;
    right: 6vw;
    bottom: 6vw;
    width: 6vw;
    height: 6vw;
}

@media print {
    @page {
        size: 297mm 210mm;
    }
    .none {
        display: block;
    }
    .page {
        position: relative;
        padding: 0;
        width: calc(297.5mm);
        height: calc(210mm);
        background-color: var(--bg-color);
        color: var(--fg-color);
        break-inside: avoid;
        margin: 0;
        left: unset;
        top: unset;
        transform: translate(0%, 0%);
    }

    .page-back-button {
        display: none;
    }

    .page-forward-button {
        display: none;
    }
}
